.about-us {
  .about-us__main {
    margin-top: 4rem;
    .about-us__header {
      font-size: 3.6rem;
      line-height: 1.3;
      font-weight: 700;
    }

    .about-us__1 {
      display: flex;
      .about-us__1-content {
        width: 40%;

        h1 {
          font-size: 3.6rem;
          line-height: 1.4;
          margin-bottom: 2rem;
        }

        p {
          font-size: 1.6rem;
          line-height: 1.7;
          margin-bottom: 2rem;
          text-align: justify;
        }
      }

      .about-us__1-image {
        width: 60%;

        img {
          margin-left: 2rem;
        }
      }
    }

    .cut {
      background: url('./../../assets/img/high-tech-background-black.jpg');
      width: 100%;
      height: 10rem;
      background-position: center center;
      background-repeat: no-repeat;
      margin-bottom: 4rem;
    }

    .about-us__2 {
      // background: url('./../../assets/img/sun.jpg');
      // height: 60vh;
      position: relative;
      margin-bottom: 10rem;

      section {
        display: flex;
        justify-content: space-between;
        text-align: justify;
        // color: white;
        z-index: 100;

        div {
          width: 30%;

          h1 {
            font-size: 2.8rem;
            margin-bottom: 1rem;
            line-height: 1.2;
          }

          p {
            font-size: 1.4rem;
          }
        }
      }
    }

    .about-us-4 {
      background: url('./../../assets/img/map-background.png');
      background-position: center center;

      .about-us-4__content {
        display: flex;
        justify-content: space-between;

        .map-image {
          background: url('./../../assets/img/vn-rs.png');
          height: 42.3rem;
          width: 76rem;
        }

        .map-content {
          background: transparent;
          width: 30%;
        }

        h1 {
          font-size: 2.6rem;
          margin-bottom: 2rem;
        }

        .el-2 {
          display: flex;
          align-items: center;
          margin-bottom: 1rem;

          .detail-content {
            font-size: 1.6rem;
            margin-left: 1rem;
          }
        }
      }
    }
  }
}
