.product-facilities {
  .product-facilities__1 {
    margin-top: 4rem;
    margin-bottom: 4rem;
    display: flex;
    justify-content: space-between;

    .product-facilities__content {
      width: 49%;

      h1 {
        font-size: 3.6rem;
        line-height: 1.3;
        margin-bottom: 2rem;
      }

      .detail-content {
        font-size: 1.6rem;
        line-height: 1.8;
      }

      .factory-content,
      .research-content {
        display: flex;
        align-items: center;
      }

      .icon-content {
        margin-right: 1.6rem;
      }
    }
  }

  .product-facilities__2 {
    position: relative;

    .product-image {
      overflow: hidden;
      position: inherit;
      img {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translateX(-50%) translateY(-50%);
        width: 100%;
        height: auto;
        vertical-align: middle;
      }
    }

    .product-content {
      margin-top: 4rem;
      font-size: 2.4rem;
      text-align: center;
    }
  }
}
